<script>
import { store } from "@/store/store.js";

export default {
  name: "MainHeader",
  data() {
    return {
      headerState: store.state,
      lightLogo: require("@/assets/images/logo.svg"),
      darkLogo: require("@/assets/images/logo-dark.svg"),
      showResponsiveNav: false
