</template>


<script>
import "@/compiled-icons/";

export default {
  name: "infoDetails",
  components: {},
  data() {
    return {
      currentTab: 1,
      interval: 0,
      windowWidth: 0
    };
  },
  methods: {
    tabTrigger(e) {
      this.currentTab = e;
    },
    setActiveTab() {
      let tabsNumber;
      if (this.windowWidth > 375) {
        tabsNumber = 4;
      } else {
        tabsNumber = 3;
      }
      this.currentTab++;
      if (this.currentTab > tabsNumber) {
        this.currentTab = 1;
      }
    },
    startTabsSlide() {
      this.interval = setInterval(this.setActiveTab, 3000);
    },
    stopTabsSlide() {
      clearInterval(this.interval);
    },
    handleResize() {
      this.windowWidth = window.innerWidth;
    }
  },
  beforeDestroy() {
    this.stopTabsSlide();
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.startTabsSlide();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  }
};
</script>

