<template lang='pug'>
.contact(id="contact")
  .container
    .contact__inner
      .contact__column.contact__column--first 
        .contact__column-inner
          .contact__title
            h2 Contact Us
            p How can we help you increase sales and grow your brand?
          .contact__form
            ul.contact__form-type
              li(:class="{'is-active' : contactInfo.isBusinessContact === true}" @click="contactInfo.isBusinessContact = true") Business
              li(:class="{'is-active' : contactInfo.isBusinessContact === false}" @click="contactInfo.isBusinessContact = false") Consumer
            .contact__form-row 
              .contact__form-row-column              
                base-input(
                  type="text"
                  name="first_name"
                  placeholder="* First name"
                  v-model='contactInfo.firstName'
                  v-validate="'required|max:256'"
                  data-vv-as="First name")
                .input-validation(v-if="errors.has('first_name')") {{errors.first('first_name')}}
              .contact__form-row-column    
                base-input(
                  type="text"
                  name="last_name"
                  placeholder="* Last name"
                  v-model='contactInfo.lastName'
                  v-validate="'required|max:256'"
                  data-vv-as="Last name")
                .input-validation(v-if="errors.has('last_name')") {{errors.first('last_name')}}
            .contact__form-row      
              .contact__form-row-column        
                base-input(
                  type="text"
                  name="work_email"
                  placeholder="* Work email"
                  v-model='contactInfo.workEmail'
                  v-validate="'required|email'"
                  data-vv-as="Work email")
                .input-validation(v-if="errors.has('work_email')") {{errors.first('work_email')}}
              .contact__form-row-column    
                base-input(
                  type="text"
                  name="website_url"
                  placeholder="* Website URL"
                  v-model='contactInfo.websiteUrl'
                  v-validate="'required'"
                  data-vv-as="Website URL")
                .input-validation(v-if="errors.has('website_url')") {{errors.first('website_url')}}
            .contact__form-row  
              base-dropdown(
                @dropdown:selected="getChooseItem"
                placeholder="What inspired you to reach out to Garden Weather?"
                :data="dropdownList")

      .contact__column.contact__column--second
        .contact__column-inner
          .contact__message-title 
            h3 Message
          .contact__form
            base-textarea(
              type="text"
              name="message"
              placeholder="Tell us about yourself so we can customize your Garden Weatherdemo for your business..."
              v-model='contactInfo.message')
            button.btn(@click="formSubmit") Contact Us
          

</template>


<script>
import "@/compiled-icons/";
import BaseInput from "@/components/base/Input.vue";
import BaseTextarea from "@/components/base/Textarea.vue";
import BaseDropdown from "@/components/base/Dropdown";
import { api } from "@/axios";

export default {
  $_veeValidate: {
    validator: "new"
  },
  name: "contactForm",
  components: {
    BaseInput,
    BaseDropdown,
    BaseTextarea
  },
  data() {
    return {
      contactInfo: {
        firstName: null,
        lastName: null,
        workEmail: null,
        websiteUrl: null,
        message: null,
        inspiration: null,
        isBusinessContact: true
      },
      dropdownList: [
        "Interested in getting more information about business products",
        "I saw your ad and wanted to get more information",
        "I read one of your articles and wanted more information",
        "I want to collaborate",
        "I have a question",
        "Other"
      ]
    };
  },
  methods: {
    getChooseItem(index) {
      this.contactInfo.inspiration = index;
    },
    formSubmit() {
      let _self = this;
      this.$validator.validateAll().then(valid => {
        if (!valid) {
          return;
        } else {
          let currentObj = this;
          api
            .post("/garden/contact", {
              first_name: this.contactInfo.firstName,
              last_name: this.contactInfo.lastName,
              work_email: this.contactInfo.workEmail,
              website_url: this.contactInfo.websiteUrl,
              message: this.contactInfo.message,
              inspiration: this.contactInfo.inspiration,
              is_business_contact: this.contactInfo.isBusinessContact
            })
            .then(function(response) {
              currentObj.output = response.data;
              _self.$router.push("/thank-you");
            })
            .catch(function(error) {
              currentObj.output = error;
            });
        }
      });
    }
  }
};
</script>

