<template lang="pug">
  .input-field(:class="[activeInput ? 'input-field--active' : '', customClass] ")
    input(
      :value="value"
      :type="type"
      @input="updateVal"
      @focus="activeInput=true"
      @blur="sendInput"
      :placeholder="placeholder"
      :disabled="disable"
      :name="name"
    )
    //- .input-validation(v-if="`errors.has(${name})`") {{errors.first(`${name}`)}}
</template>

<script>
export default {
  // inject: {
  //   $validator: "$validator"
  // },
  props: {
    name: {
      type: String
    },
    value: {
      type: [String, Number],
      default: ""
    },
    type: {
      type: String,
      required: true
    },
    error: {
      type: String
    },
    customClass: {
      type: [String, Array]
    },
    placeholder: {
      type: String
    },
    disable: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeInput: false
    };
  },
  methods: {
    sendInput(e) {
      this.activeInput = false;
      this.$emit("input:save", e.target.value);
    },
    updateVal(e) {
      this.$emit("input", e.target.value);
    }
  }
};
</script>

<style lang="scss" scoped>
.input-field {
  position: relative;
  width: 100%;
  margin-bottom: 40px;
  display: block;
  text-align: left;
  @include breakpoint($semi) {
    margin-bottom: 52px;
  }
  @media only screen and (max-width: 500px) {
    margin-bottom: 40px;
  }

  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="number"] {
    outline: none;
    height: 34px;
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.5);
    font-size: em(18);
    width: 100%;
    color: rgba(51, 51, 51, 1);
    @media only screen and (max-width: 500px) {
      font-size: em(16);
    }
  }

  input {
    @include placeholder {
      opacity: 1;
      color: rgba(51, 51, 51, 0.25);
    }
  }
}
</style>
