<template lang="pug">
  .input-field(:class="[activeInput ? 'input-field--active' : '', customClass] ")
    textarea(
      :type="type"
      @input="updateVal"
      @focus="activeInput=true"
      @blur="sendInput"
      :placeholder="placeholder"
      :disabled="disable"
      :name="name"
    )
    //- .input-validation(v-if="`errors.has(${name})`") {{errors.first(`${name}`)}}
</template>

<script>
export default {
  // inject: {
  //   $validator: "$validator"
  // },
  props: {
    name: {
      type: String
    },
    type: {
      type: String,
      required: true
    },
    error: {
      type: String
    },
    customClass: {
      type: [String, Array]
    },
    placeholder: {
      type: String
    },
    disable: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeInput: false
    };
  },
  methods: {
    sendInput(e) {
      this.activeInput = false;
      this.$emit("input:save", e.target.value);
    },
    updateVal(e) {
      this.$emit("input", e.target.value);
    }
  }
};
</script>

<style lang="scss" scoped>
.input-field {
  position: relative;
  width: 100%;
  margin-bottom: 40px;
  display: block;
  text-align: left;

  textarea {
    outline: none;
    height: 257px;
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.5);
    font-size: em(18);
    line-height: line-height(18, 24);
    width: 100%;
    color: rgba(51, 51, 51, 1);
    resize: none;
    @include placeholder {
      opacity: 1;
      color: rgba(51, 51, 51, 0.25);
    }
    @include breakpoint($semi) {
      font-size: em(14);
      line-height: line-height(14, 19);
      height: 84px;
    }
  }
}
</style>
