<template lang="pug">
  .list-dropdown(:class="[open ? 'is-active' : '', customClass] ")
    .list-dropdown__trigger(v-click-outside="closeDropdown"  @click="openDropdown")
      span.list-dropdown__label(:key="Math.random(100)") {{currentLabel}}
    transition(name="dropdown-animation")
      .list-dropdown__content(v-show="open")
        p.list-dropdown__no-data(v-if="data == undefined || data.length == 0") {{noDataLabel}}
        ul.list-dropdown__list-wrapp
          li.list-dropdown__list-item(v-for="(item, index) in data" :key="index" @click="select(item, index)")  {{item}}
</template>
 
 <script>
import ClickOutside from "vue-click-outside";

export default {
  props: {
    placeholder: {
      type: String
    },
    data: {
      type: Array
    },
    customClass: {
      type: String
    },
    name: {
      type: String
    },
    noDataLabel: {
      type: String
    }
  },
  data() {
    return {
      open: false,
      choosenItem: null
    };
  },
  methods: {
    closeDropdown() {
      this.open = false;
    },
    openDropdown() {
      this.open = !this.open;
    },
    select(item, index) {
      this.choosenItem = item;
      this.value = "selected";
      this.$emit("dropdown:selected", index);
    }
  },
  computed: {
    currentLabel: {
      cache: false,
      get() {
        return this.choosenItem == null ? this.placeholder : this.choosenItem;
      }
    }
  },
  directives: {
    ClickOutside
  }
};
</script>

<style lang="scss" scoped>
.dropdown-animation-enter-active,
.dropdown-animation-leave-active {
  transition: $t-hover;
}

.dropdown-animation-enter,
.dropdown-animation-leave-to {
  transform: translateY(-5px);
  opacity: 0;
}

.list-dropdown {
  position: relative;
  background-color: white;
  width: 100%;

  &.is-active {
    &:after {
      transform: translate(-50%, -50%) rotate(-45deg);
      margin-top: 4px;
    }
  }

  &:after {
    position: absolute;
    top: 50%;
