    content: "";
    width: 13px;
    height: 13px;
    border-width: 1px 1px 0 0;
    border-color: #333333;
    border-style: solid;
    transform: translate(-50%, -50%) rotate(135deg);
    margin-top: -2px;
    transition: $t-hover;
    @media only screen and (max-width: 500px) {
      top: auto;
      bottom: 9px;
    }
  }

  &__trigger {
    cursor: pointer;
    padding: 0 33px 0 0;
    z-index: 2;
